/* header */
.header {
  height: var(--header-footer-height);
  padding: 10px 0px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.footer {
  height: var(--header-footer-height);
  padding: 10px 0px;
  background: var(--black-primary);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white-primary);
}

.footer__content {
  text-align: center;
}
.footer__content .logo {
  display: inline-block;
}

/* title (h1) */
.title {
  font-style: normal;
  font-weight: 600;
  font-size: 45px;
  line-height: 76px;
  color: var(--black-primary);
  position: relative;
}
.title_line {
  position: relative;
  text-align: center;
}
.title_line::before {
  content: "";
  display: block;
  position: absolute;
  background-color: var(--accent-color);
  width: 80px;
  height: 3px;
  left: calc(50% - 40px);
  bottom: -5px;
  border-radius: 5px;
}
@media (min-width: 750px) {
  .title {
    font-size: 54px;
  }
}

/* subtitle (h2) */
.subtitle {
  font-style: normal;
  font-weight: 600;
  font-size: 34px;
  line-height: 54px;
  color: var(--black-primary);
  position: relative;
}
.subtitle_line {
  position: relative;
  text-align: center;
}
.subtitle_line::before {
  content: "";
  display: block;
  position: absolute;
  background-color: var(--accent-color);
  width: 60px;
  height: 3px;
  left: calc(50% - 30px);
  bottom: -5px;
  border-radius: 5px;
}

/* text */
.desc {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
}

/* logo */
.logo {
  font-size: 20px;
  white-space: nowrap;
  font-weight: 600;
  z-index: var(--zIndexLogo);
}
.logo__link {
  text-decoration: none;
  color: inherit;
  transition: color var(--transition-duration);
}
.logo__link:hover, .logo__link:focus {
  color: var(--accent-color);
}

.btn-base {
  padding: 10px 22px;
  cursor: pointer;
  border-radius: 7px;
  font-family: var(--font-inter);
  background-color: var(--blue-primary);
  color: var(--white-primary);
  transition: background-color var(--transition-duration);
  font-weight: 500;
  min-width: 70px;
}
.btn-base:hover {
  background-color: var(--blue-primary-hover);
}
.btn-base:active {
  background-color: var(--blue-primary-active);
}
.btn-base:focus {
  background-color: var(--blue-primary-focus);
}

.btn-accent {
  padding: 7px 14px;
  cursor: pointer;
  border-radius: 7px;
  font-family: var(--font-inter);
  background-color: var(--accent-color);
  color: var(--white-primary);
  transition: background-color var(--transition-duration);
  font-weight: 500;
}
.btn-accent:hover {
  background-color: var(--accent-color-hover);
}
.btn-accent:active {
  background-color: var(--accent-color-active);
}
.btn-accent:focus {
  background-color: var(--accent-color-focus);
}

/* input */
.input-base {
  padding: 10px 50px 10px 20px;
  outline: none;
  border: none;
  color: var(--black-primary);
  border: 1px solid var(--grey-color);
  transition: box-shadow var(--transition-duration);
  height: 50px;
  font-size: var(--inputFontSize);
  border-radius: 7px;
  outline: none;
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.07);
}
.input-base:hover {
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.02);
}
.input-base:focus {
  box-shadow: inset 1px 2px 4px rgba(0, 0, 0, 0.01), 0px 0px 8px rgba(0, 0, 0, 0.2);
}
.input-base_error {
  border: 1px solid var(--attention-color);
}
.input-base_error + i {
  color: var(--attention-color);
}
.input-base_approve {
  border: 1px solid var(--approve-color);
}
.input-base_approve + i {
  color: var(--approve-color);
}

.input-base_disabled {
  background: var(--disabled-color);
  pointer-events: none;
  box-shadow: none;
}

.error-text {
  position: absolute;
  color: var(--attention-color);
  left: 0;
  bottom: -20px;
  opacity: 0;
  transition: opacity var(--transition-duration);
}
.error-text_active {
  opacity: 1;
}

/* tippy box styles*/
.tippy-box[data-theme~=white] {
  background-color: var(--grey-color);
  color: var(--black-primary);
}

.tippy-box[data-theme~=white][data-placement^=top] > .tippy-arrow::before {
  border-top-color: var(--grey-color);
}

.tooltip-text {
  font-size: 13px;
  text-align: center;
}

.hero-form__tooltip-box {
  display: none;
}

.hero-form__question {
  display: inline-block;
}

/* hero bg (blue) */
.first-screen-bg {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  min-height: 100vh;
  height: 100%;
  background: var(--blue-primary);
  z-index: -1;
}

/* hero section */
.hero {
  padding-top: var(--header-footer-height);
  min-height: 100vh;
}

.hero-form {
  background: var(--white-primary);
  padding: 20px;
  min-height: 300px;
  border-radius: 15px;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  margin: 40px 0px;
}

.hero-form__title,
.hero-form__subtitle {
  text-align: center;
}

.hero-form__label {
  display: flex;
  flex-direction: column;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 20px;
  padding-top: 10px;
}

.hero-form__text {
  align-self: flex-start;
}

.hero-form__wrap_timebox {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
}

.input-group {
  position: relative;
}

.hero-form__input_link {
  width: 100%;
}

.hero-form__input-icon_link {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.checkbox {
  display: none;
}

.checkbox_fake {
  display: block;
  float: left;
  width: 20px;
  border-radius: 5px;
  height: 20px;
  border: 2px solid var(--blue-primary);
  position: relative;
}
.checkbox_fake::before {
  content: "✔";
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  border-radius: 2px;
  color: var(--approve-color);
  opacity: 0;
}

.checkbox:checked + .checkbox_fake::before {
  opacity: 1;
}

.checkbox-label {
  display: inline-block;
  margin: 0 auto;
  margin-bottom: 15px;
}

.checkbox__text {
  margin-left: 10px;
}

.hero-form__question-box {
  justify-content: center;
  display: flex;
  align-items: center;
  gap: 15px;
}

.hero-form__label {
  position: relative;
}

.hero-form__wrap {
  display: flex;
  flex-direction: column;
}

.hero-form__subtitle {
  margin-bottom: 15px;
}